.card {
    flex-shrink: 1;
    display: flex;
    align-items: center;
    width: 300px;
    border-radius: 0.8rem;
    margin: 6rem;
    box-shadow: 0 0 6px 4px rgba(0, 0, 0, .6);
    transition: transform 1s ease;
    cursor: pointer;
}

img {
    width: 6rem;
    height: 6rem;
    padding: 0.6rem;
}

h3 {
    margin: auto;
    font-size: 1.5rem;
}

.card:hover {
    transform: translateY(-4px);
    box-shadow: 0 0 6px 4px rgba(0, 78, 146, .8);
}